@import '../../style/vars';

.comics-store {
    position: relative;
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas:
    ". a a ."
    ". a a .";
    gap: 50px 50px;
    padding-bottom: 130px;

    @media (max-width: 1100px) {
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;
    }

    @media (max-width: 800px) {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }

    @media (max-width: 500px) {
        flex-direction: column;
        align-items: center;
        display: flex;
        justify-content: center;
    }

    .comics__item {
        display: flex;
        flex-direction: column;
        gap: 10px;
        transform: translateY(0);
        transition: all 0.5s ease;
        filter: grayscale(.6);

        & .item-img {
            
            & img {
                width: 225px;
                height: 345px;
                object-fit: contain;
            }
        }
    
        & .price {
            color: #00000099;
            font-family: 'Roboto Condensed';
            font-weight: 700;
            font-size: 0.85rem;
        }
    
        & .title {
            font-size: 0.78rem;
        }
    
        & .back {
            background: none;
            font-size: 0.64rem;
            font-weight: 700;
        }

        &:hover {
            cursor: pointer;
            filter: grayscale(0);
            transform: translateY(-20px);
        }
    }

}
.comics-btn {
    background-color: #9F0013;
    color: #fff;
    text-transform: uppercase;
    position: absolute;
    bottom: 50px;
    // margin-bottom: 50px ;
    left: 50%;
    transform: translateX(-50%);
    width: 170px;
    transition: all 0.3s ease;

    &:hover,&:active,&:focus {
        background-color: #741e28;
        transform: translateX(-50%);
    }
    
    &:disabled {
        filter: grayscale(.7)
    }
}

.loading-block {
    max-width: 1100px;
    display: flex;
    justify-content: center;
    align-items: center;
